import React, { useState, useEffect } from "react";
import Unity, { UnityContext } from "react-unity-webgl";
import background from "./background.png";
import "./App.css";
import { initializeApp } from "firebase/app";
import { getAnalytics } from "firebase/analytics";
import { useMoralis } from "react-moralis";
import { useWallet } from '@solana/wallet-adapter-react';
import {
  getAllStakedNFTs, getAllNFTs
} from './fetch-accounts';
import AgoraRTC from "agora-rtc-sdk-ng";

require('@solana/wallet-adapter-react-ui/styles.css');

var allNftData;

var loc = window.location.href + "";
if (loc.indexOf("http://") === 0) {
  window.location.href = loc.replace("http://", "https://");
}

const unityContext = new UnityContext({
  loaderUrl: "Build/unity.loader.js",
  dataUrl: "Build/unity.data",
  frameworkUrl: "Build/unity.framework.js",
  codeUrl: "Build/unity.wasm",
});

const firebaseConfig = {
  apiKey: "AIzaSyDcvry5_ArzMtQPFKAqawHgnAZuBusa_Ws",
  authDomain: "moonland-8788b.firebaseapp.com",
  projectId: "moonland-8788b",
  storageBucket: "moonland-8788b.appspot.com",
  messagingSenderId: "533308612802",
  appId: "1:533308612802:web:2f02128e0d471808d3db5b",
  measurementId: "G-GQHPLJ7Y5M",
};

const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);

const Content = () => {
  let rtc = {
    localAudioTrack: null,
    client : null
  };
  let rtcOptions = {
    appId:"31f1e7faadab478baf5aa24264afe1d9",
    channel:"Room1",
    token:"00631f1e7faadab478baf5aa24264afe1d9IAA3mAy2xxWwN81MJO3fFa4w2GbDcURllZI+oeE4wN3XPS4VsiUAAAAAEAAU3oYIwn/eYgEAAQDAf95i",
    uid:""
  };
  async function startBasicCall()
  {
    rtc.client = AgoraRTC.createClient({mode: "rtc", codec:"vp8"});
    rtc.client.on("user-published", async (user, mediaType) => {
      await rtc.client.subscribe(user, mediaType);
      if(mediaType === "audio")
      {
        const remoteAudioTrack = user.audioTrack;
        remoteAudioTrack.play();
      }
    });

    rtc.client.on("user-unpublished", async (user) => {
      await rtc.client.unsubscribe(user);
    });
  }

  async function joinCall()
  {
    rtcOptions.uid = await rtc.client.join(rtcOptions.appId, rtcOptions.channel, rtcOptions.token);
    rtc.localAudioTrack = await AgoraRTC.createMicrophoneAudioTrack();
    await rtc.client.publish([rtc.localAudioTrack]);
  }

  async function leaveCall()
  {
    rtc.localAudioTrack.close();
    await rtc.client.leave();
  }

  const [isLoadingNFT, setIsLoadingNFT] = useState(false);
  const [isLoadingStakedNFT, setIsLoadingStakedNFT] = useState(false);
  const [publicKey, setPublicKey] = useState("");

  const wallet = useWallet();

  const Moralis = useMoralis();

  async function letsgo() {
    if(!isLoadingNFT)
    {
      if(allNftData === undefined)
      {
        allNftData = "";
      }
      if(wallet !== undefined)
      {
        setIsLoadingNFT(true);
        if(publicKey !== "" && publicKey !== undefined)
        {
          getNftTokenData();
        }
        else
        {
          setIsLoadingNFT(false);
          console.log("Wallet not connected");
        }
      }
    }
  }

  async function letsgoStaked() {
    if(!isLoadingStakedNFT)
    {
      if(wallet !== undefined)
      {
        setIsLoadingStakedNFT(true);
        if(publicKey !== "" && publicKey !== undefined)
        {
          getAllStackedNftData();
        }
        else
        {
          setIsLoadingStakedNFT(false);
          console.log("Wallet not connected");
        }
      }
    }
  }

  const getAllStackedNftData = async () => {
    try
    {
      var root = {
        data: {
          arr: [],
        },
      };

      console.log("All Staked");
      if(wallet === undefined)
      {
        setIsLoadingStakedNFT(false);
        return;
      }

      const nftMetaDataAccounts = await getAllStakedNFTs(publicKey.toString());

      if(nftMetaDataAccounts.length !== 0)
      {
        for(let i = 0; i < nftMetaDataAccounts.length; i++)
        {
          await Moralis.Moralis.Cloud.run("fetchJSON", {theURL : nftMetaDataAccounts[i].data.uri}).then((result) => {
            if(allNftData !== undefined && allNftData !== "")
            {
              root.data = JSON.parse(JSON.stringify(allNftData));
            }
            if(result.text !== "" && result.text !== undefined)
            {
              const obtainedJSON = JSON.parse(result.text);
              const toSendJSON = { data : obtainedJSON};
                const dataToAdd = JSON.parse(JSON.stringify(toSendJSON));
                if(root.data.arr.includes(dataToAdd) === false)
                {
                  root.data.arr.push(dataToAdd);
                  allNftData = root.data;
                }
            }
    
            if(allNftData !== undefined && allNftData !== "")
            {
              unityContext.send("_BRIDGE_", "SetNFTData", JSON.stringify(allNftData));
            }
          });
        }
        setIsLoadingStakedNFT(false);
      }
      else
      {
        console.log("No Stacked NFTs found");
        setIsLoadingStakedNFT(false);
      }
    }
    catch(error)
    {
      console.log(error);
    }
  };

  const getNftTokenData = async () => {
    try
    {
      setIsLoadingNFT(true);
      var root = {
        data: {
          arr: [],
        },
      };

      console.log("All Simple");
      if(wallet === undefined)
      {
        setIsLoadingNFT(false);
        return;
      }

      const nftMetaDataAccounts = await getAllNFTs(publicKey.toString());

      if(nftMetaDataAccounts.length !== 0)
      {
        for(let i = 0; i < nftMetaDataAccounts.length; i++)
        {
          // console.log("Fetching NFT Index : " + i);
          await Moralis.Moralis.Cloud.run("fetchJSON", {theURL : nftMetaDataAccounts[i].data.uri}).then((result) => {
            if(allNftData !== undefined && allNftData !== "")
            {
              root.data = JSON.parse(JSON.stringify(allNftData));
            }
            if(result.text !== "" && result.text !== undefined)
            {
              const obtainedJSON = JSON.parse(result.text);
              const toSendJSON = { data : obtainedJSON};
              const dataToAdd = JSON.parse(JSON.stringify(toSendJSON));
              if(root.data.arr.includes(dataToAdd) === false)
              {
                root.data.arr.push(dataToAdd);
                allNftData = root.data;
              }
            }
    
            if(allNftData !== undefined && allNftData !== "")
            {
              unityContext.send("_BRIDGE_", "SetNFTData", JSON.stringify(allNftData));
            }
          });
        }
        setIsLoadingNFT(false);
      }
      else
      {
        console.log("No NFTs found");
        setIsLoadingNFT(false);
      }
    }
    catch(error)
    {
      console.log(error);
    }
  };

  useEffect(function () {
    unityContext.on("STAKED_NFT", function () {
      if(isLoadingStakedNFT)
      {
        if(allNftData !== undefined)
        {
          if(allNftData !== "")
          {
            unityContext.send("_BRIDGE_", "SetNFTData", JSON.stringify(allNftData));
          }
        }
      }
      else
      {
        letsgoStaked();
      }
    });
  });

  useEffect(function () {
    unityContext.on("NFT", function () {
      if(isLoadingNFT)
      {
        if(allNftData !== undefined)
        {
          if(allNftData !== "")
          {
            unityContext.send("_BRIDGE_", "SetNFTData", JSON.stringify(allNftData));
          }
        }
      }
      else
      {
        letsgo();
      }
    });
  });

  useEffect(function(){
    unityContext.on("ConnectWallet", function(index){
      setPublicKey("");
      for(let i = 0; i < wallet.wallets.length; i++)
      {
        if(wallet.wallets[i].adapter.connected)
        {
          wallet.wallets[i].adapter.disconnect();
        }
      }
      wallet.wallet = wallet.wallets[index];
      if(wallet.wallet.adapter.connected)
      {
        //Wallet Connected successfully
        setPublicKey(wallet.wallet.adapter.publicKey);
      }
      else
      {
        wallet.wallet.adapter.connect().then(()=> {
          //Wallet Connected successfully
        setPublicKey(wallet.wallet.adapter.publicKey);
        }, (error) => {
          //Wallet Connection failed
          console.log("Connection Failed" + error);
          unityContext.send("_BRIDGE_", "OnWalletConnectFailed", JSON.stringify(error));
        }).catch((error) => {
          //Wallet Connection failed
          console.log("Connection Failed" + error);
          unityContext.send("_BRIDGE_", "OnWalletConnectFailed", JSON.stringify(error));
        });
      }
    });
  });

  useEffect(() => {
    if(publicKey !== undefined && publicKey !== "")
    {
      console.log("Connection Successful" + publicKey);
      joinCall();
      unityContext.send("_BRIDGE_", "OnWalletConnectSuccessful", JSON.stringify(publicKey));
    }
  }, [publicKey]);

  useEffect(function () {
    unityContext.on("Logout", function () {
      if (window.solana.isConnected) {
        window.solana.disconnect();
        window.solana.on("disconnect", () => console.log("disconnected!"));
        unityContext.send("SolanaWalletConnect", "Disconnected");
      }
    });
  }, []);

  const [isLoaded, setIsLoaded] = useState(false);
  const [progression, setProgression] = useState(0);

  useEffect(function () {
    unityContext.on("progress", function (progression) {
      setProgression(parseFloat(progression * 100).toFixed(2));
    });
  }, []);

  useEffect(function () {
    unityContext.on("loaded", function () {
      setIsLoaded(true);
      startBasicCall();
    });
  }, []);

  return (
    <div>
      <div
        style={{
          visibility: isLoaded ? "hidden" : "visible",
          backgroundImage: `url(${background})`,
          backgroundSize: "cover",
          height: "100%",
          width: "100%",
          color: "#f5f5f5",
          position: "absolute",
          left: 0,
          top: 0,
        }}
      >
        <p
          style={{
            visibility: isLoaded ? "hidden" : "visible",
            height: "30px",
            width: "100%",
            position: "absolute",
            textAlign: "center",
            bottom: "100px",
            fontSize: "2em",
            fontColor: "red",
          }}
        >
          Loading... {progression}%
        </p>

        <div>
          <Unity
            style={{
              visibility: isLoaded ? "visible" : "hidden",
              height: "100%",
              width: "100%",
              background: "grey",
              position: "absolute",
              left: 0,
              top: 0,
              overflow: "hidden",
            }}
            unityContext={unityContext}
          />
        </div>
      </div>
    </div>
  );
};

function App() {
  return (
    <Content/>
  );
}

export default App;